    grid-template-columns: repeat(2, 1fr);
  }
  .social {
    text-align: start;
  }
  .logos {
    justify-content: start;
  }
}

@media (max-width: 1120px) {
  .hero {
    flex-wrap: wrap;
  }
  .hero-title {
    font-size: 3rem;
  }
  .hero-text {
    font-size: 1.5rem;
  }
  .hero-button {
    width: 100%;
    font-size: 2rem;
    padding: 1rem;
  }
  .cart-container {
    width: 88%;
  }
  .detail-container {
    width: 100%;
    height: min-content;
    flex-wrap: wrap;
    justify-content: center;
    padding: 0;
  }
  .description-detail {
    width: 80%;
  }
  .image-detail-view {
    margin-top: 1rem;
  }
  .product-details {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .product-title {
    font-size: 1.2rem;
  }

  .product-details button {
    width: 300px;
  }
  .product-details > button:nth-child(1) {
    width: 300px;
  }

  .image-detail {
    width: 275px;
  }
}

@media (max-width: 760px) {
  .hero > * {
    line-height: 2;
  }
  .footer-content {
    line-height: 3;
    grid-template-columns: 1fr;
  }
  .nav-title {
    display: none;
  }
  nav {
    font-size: 0.75rem;
  }
}

