    width: 80%;
  }
  .quantity > * {
    padding: 0.25rem 0.75rem;
  }
  .image-detail {
    width: 230px;
  }
}

@media (max-width: 310px) {
  .hero-title {
    font-size: 3rem;
  }
  .cart-container {
    width: 100%;
  }

  #root > div > nav > div > a {
    padding-right: 1rem;
  }

  .buttons > button {
    width: 200px;
  }
  img.logo-image {
    display: none;
  }
  .links {
    width: 100%;
  }
  .products {
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  }
  .image-detail {
    width: 150px;
  }
  .product-details > button:nth-child(1) {
    width: 90%;
  }
  .product-title {
    font-size: medium;
    text-align: center;
  }
  .product-details > button {
    width: 90%;
    font-size: medium;
  }
}
